<template>
	<div class="navigation">
		<div id="mobile-menu-open" class="shadow-large" @click="onOpenMobileMenu">
			<i class="fa fa-bars" aria-hidden="true"></i>
		</div>
		<!-- End #mobile-menu-toggle -->
		<header v-if="isMenuVisible">
			<div id="mobile-menu-close" @click="onCloseMobileMenu">
				<span>Close</span>
				<i class="fa fa-times" aria-hidden="true"></i>
			</div>
			<ul id="menu" class="shadow">
				<li @click="onClickNavigation('about')">
					<a title="about">About</a>
				</li>
				<li @click="onClickNavigation('experience')">
					<a title="experience">Experience</a>
				</li>
				<li @click="onClickNavigation('education')">
					<a title="education">Education</a>
				</li>
				<li @click="onClickNavigation('projects')">
					<a title="projects">Projects</a>
				</li>
				<li @click="onClickNavigation('articles')">
					<a title="articles">Articles</a>
				</li>
				<li @click="onClickNavigation('skills')">
					<a title="skills">Skills</a>
				</li>
				<li>
					<a href="#contact" title="contact">Contact</a>
				</li>
			</ul>
		</header>
	</div>
</template>

<script>
	let self = null;
	export default {
		name: "navigation",

		data() {
			return {
				isMenuVisible: document.getElementsByTagName("body")[0].clientWidth > 770
			};
		},

		created() {
			this.initResizeObserver();
			self = this;
		},

		methods: {
			onClickNavigation(id) {
				if (document.getElementsByTagName("body")[0].clientWidth < 770)
					this.isMenuVisible = false;
				document.getElementById(id).scrollIntoView({
					behavior: "smooth"
				});
			},
			onOpenMobileMenu() {
				this.isMenuVisible = true;
			},

			onCloseMobileMenu() {
				this.isMenuVisible = false;
			},

			initResizeObserver() {
				const resizeObserver = new ResizeObserver(() => {
					self.isMenuVisible =
						document.getElementsByTagName("body")[0].clientWidth > 770;
				});

				resizeObserver.observe(document.body);
			}
		}
	};
</script>

<style lang="scss" scoped>
	li {
		cursor: pointer;
	}
</style>