import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home";
/*VisitorData*/

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      component: Home,
      name: "home",
      meta: {
        title: "Brent Vanwildemeersch | Portofolio",
      },
    },
  ],
  mode: "hash",
  linkExactActiveClass: "active",
});
